class Bubbles {
    constructor(x, y, radius) {
      this.x = x;
      this.y = y;
      this.radius = radius;
      this.color = {
        bg: "rgba(255, 255, 255, 0.45)"
      };
      this.velocity = {
        x: (Math.random() - 0.5) * 0.5,
        y: Math.random() * 2
      };
      this.opacity = 1;
    }
  }
  
  class miniBubbles {
    constructor(x, y, radius) {
      this.x = x;
      this.y = y;
      this.radius = radius;
      this.color = {
        bg: "rgba(255, 255, 255, 1)"
      };
      this.velocity = {
        x: (Math.random() - 0.5) * 0.6,
        y: (Math.random() - 1) * 0.5
      };
      this.gravity = -0.03;
      this.timeToLive = 500;
    }
  }
  
  let canvas = null;
  let canvas2d = null;
  let mouse = {
    x: 0,
    y: 0
  };
  let spawnRate = 99;
  let timer = 0;
  let minibubbles = [];
  let bubbles = [];
  let backgroundGradient = null;
  
  function Bobo() {
    canvas = document.querySelector("canvas");
    canvas2d = canvas.getContext("2d");
    canvas.height = innerHeight * 0.5;
    canvas.width = innerWidth;
    backgroundGradient = canvas2d.createLinearGradient(
      0,
      0,
      0,
      canvas.height
    );
    backgroundGradient.addColorStop(0, "#89D1E4");
    backgroundGradient.addColorStop(1, "#0A6092");
    Bubbles.prototype.draw = function () {
      canvas2d.beginPath();
      canvas2d.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false);
      canvas2d.fillStyle = this.color.bg;
      canvas2d.fill();
      canvas2d.closePath();
    };
  
    Bubbles.prototype.update = function () {
      this.y -= this.velocity.y;
      this.draw();
    };
  
    miniBubbles.prototype.draw = function () {
      canvas2d.save();
      canvas2d.beginPath();
      canvas2d.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false);
      canvas2d.fillStyle = this.color.bg;
      canvas2d.fill();
      canvas2d.closePath();
      canvas2d.restore();
    };
    miniBubbles.prototype.update = function () {
      if (this.y - this.radius < 0) {
        this.velocity.y = this.velocity.y;
      } else {
        this.velocity.y += this.gravity;
      }
      this.x += this.velocity.x;
      this.y += this.velocity.y;
      this.timeToLive -= 1;
      this.draw();
    };
    canvas.addEventListener("mousemove", mouseMove);
    this.animate = function() {
      animate();
    }
    this.init = init();
  }
  
  
  function randomIntFromRange(min, max) {
    return Math.floor(Math.random() * (max - min + 1) + min);
  }
  function init() {
    bubbles = [];
    minibubbles = [];
  }
  function mouseMove(e) {
    mouse.x = e.offsetX;
    mouse.y = e.offsetY;
  }
  
  function animate() {
    requestAnimationFrame(animate);
    //background linear gradient ------------
  
    canvas2d.fillStyle = backgroundGradient;
    // canvas2d.fillRect(0, 0, canvas.width, canvas.height);
  
    // Render the Bubbles -------------------
    bubbles.forEach((bubble, index) => {
      bubble.update();
      if (bubble.radius == 0) {
        bubbles.splice(index, 1);
      }
    });
    minibubbles.forEach((minibubble, index) => {
      minibubble.update();
      if (minibubble.timeToLive == 0) {
        minibubbles.splice(index, 1);
      }
    });
    timer++;
    if (timer % spawnRate == 0) {
      const radius = randomIntFromRange(15, 30);
      const minradius = Math.random() * 2 + 1;
      const x = Math.max(radius, Math.random() * canvas.width - radius);
      const y = innerHeight + 100;
      bubbles.push(new Bubbles(x, y, radius, "white"));
      minibubbles.push(new miniBubbles(x, y, minradius));
      spawnRate = randomIntFromRange(70, 200);
    }
  
    //  When Hover over the bubbles ------------------
    for (var i = 0; i < bubbles.length; i++) {
      // 鼠标hover 自动爆破
      if (
        mouse.x > bubbles[i].x - bubbles[i].radius &&
        mouse.x < bubbles[i].x + bubbles[i].radius // 判断水平线
      ) {
        if (
          mouse.y > bubbles[i].y - bubbles[i].radius &&
          mouse.y < bubbles[i].y + bubbles[i].radius // 判断垂直线
        ) {
          let x = bubbles[i].x;
          let y = bubbles[i].y;
          let radius = Math.random() * 2 + 1;
          bubbles[i].radius -= bubbles[i].radius;
          for (let a = 0; a < Math.random() * 4 + 4; a++) {
            minibubbles.push(new miniBubbles(x, y, radius));
          }
        }
      }
      // 到达一定高度 自动爆破
      if(bubbles[i].y < (innerHeight * 0.65)){
        let x = bubbles[i].x;
        let y = bubbles[i].y;
        let radius = Math.random() * 2 + 1;
        bubbles[i].radius -= bubbles[i].radius;
        for (let a = 0; a < Math.random() * 4 + 4; a++) {
          minibubbles.push(new miniBubbles(x, y, radius));
        }
      }
    }
  }
  


  function Drow() {

    this.canvas = document.querySelector("canvas");
    this.ctx = this.canvas.getContext("2d");
  
    this.setSize = function (width, height) {
      if (this.canvas) {
        this.canvas.width = this.width = width;
        this.canvas.height = this.height = height;
        window.width = this.width;
        window.height = this.height;
      }
    }
    this.fillPageWithCanvas = function () {
      this.setSize(window.innerWidth, window.innerHeight);
    }
  
    this.background = function (color) {
      this.ctx.fillStyle = color;
      this.ctx.fillRect(0, 0, this.width, this.height);
    }
  
    this.beginPath = function () {
      this.ctx.beginPath();
    }
  
    this.closePath = function () {
      this.ctx.closePath();
    }
  
    this.lineTo = function (x, y) {
      this.ctx.lineTo(x, y);
    }
  
    this.fill = function () {
      this.ctx.fill();
    }
  
    this.fillStyle = function (color) {
      this.ctx.fillStyle = color;
    }
  
    window.addEventListener("resize", () => {
      this.setSize(window.innerWidth, window.innerHeight);
    });
  };
  
  
  function Wave() {
    this.start = function () {
      let go = new Drow();
      let t = 0;
      go.fillPageWithCanvas();
      loop();
      function loop() {
        go.background("#fff");
        let storms = [
          function (f) {
            return Math.sin(f * 6 + t * 0.01) * 60 * Math.sin(t * 0.03);
          },
          function (f) {
            return (Math.sin(f * 6 + t * 0.01) * 100 + 20) * Math.sin(t * 0.03);
          },
          function (f) {
            return (Math.sin(f * 6 + t * 0.01) * 50 + 20) * Math.sin(t * 0.03);
          }
        ];
        let colors = ["#007bff", "#0da0bd", "#009cff"];
        for (let f = 0; f < 3; f++) {
          let color = colors[f];
          let storm = storms[f];
          go.beginPath();
          for (let e = 0; e <= 1 + 0.01; e += 0.01) {
            go.lineTo(e * go.width, go.height * 0.6 + storm(e));
          }
          go.lineTo(go.width, go.height);
          go.lineTo(0, go.height);
          go.closePath();
          go.fillStyle(color);
          go.fill();
        }
        t++;
        requestAnimationFrame(loop);
      }
    }
  }
    
  export {
    Bobo,
    Wave
  };